import { NgModule } from '@angular/core';
import { MDBBootstrapModule } from 'angular-bootstrap-md';
import { CommonModule } from '@angular/common';
import { HeaderComponent } from '@hf/components/header/header.component';
import { FooterComponent } from '@hf/components/footer/footer.component';
import { RouterModule } from '@angular/router';

const exportedComponents = [
  HeaderComponent,
  FooterComponent
];

@NgModule({
  declarations: [
    ...exportedComponents
  ],
  exports: [
    ...exportedComponents
  ],
  imports: [
    MDBBootstrapModule.forRoot(),
    CommonModule,
    RouterModule,
  ]
})
export class HeaderFooterModule { }
